
import { defineComponent, SetupContext, PropType } from 'vue';

export default defineComponent({
  name: 'BaseInputCheckbox',
  props: {
    isDone: {
      type: Boolean as PropType<boolean>,
      required: true
    }
  },
  setup(props, context: SetupContext) {
    const updateChecked = () => {
      context.emit('input');
    };

    return { updateChecked };
  }
});
